import { Link } from 'react-router-dom'
import styled from 'styled-components'

const StyledBtn = styled(Link)`
    background: none;
    font-size: 1.7rem;
    font-family: 'Anton', sans-serif;
    font-weight: 500;
    color: #2a2438;
    text-decoration: none;

`

export default StyledBtn